import React, { useState } from "react";
import { Avatar } from '@mui/material';
import {
  ArrowDownwardOutlined,
  ArrowUpwardOutlined,
  ChatBubbleOutlined,
  MoreHorizOutlined,
  RepeatOneOutlined,
  ShareOutlined,
} from '@mui/icons-material';
import Closeicon from "@mui/icons-material/Close";
import { Modal } from "react-responsive-modal";
import "react-responsive-modal/styles.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import './css/question.css'


const Question = () => {
  const [Modalopen,setModalopen] = useState(false)
  const [inputUrl, setinputurl] = useState("")
  const Close=<Closeicon/>
  const [answer, setAnswer] = useState("");
  return (
 <>
 <div className="head">
<div className="information">
  <Avatar />
  <h4>Username</h4>
  <small>
 Timestamp
  </small>
</div>
<div className="body">
  <p class="quest">This is the Question</p>
  <button className='Ansbtn' onClick={()=> (setModalopen(true)) }>Answer</button>
  <Modal
 open={Modalopen}
  Closeicon={Close}
  onClose={()=> setModalopen(false)}
  CloseonEsc
  center
  closeOnOverlayClick={false}
   styles={{
  overlay: {
 height: "auto",
  },
}}
 >
  <div className="question">
      <p>asked by </p>
    </div>
    <div className="answer">
      <ReactQuill value={answer}  placeholder="Enter your answer"/>
    </div>
    <div className="button">
      <button className="cancle" onClick={()=> setModalopen(false)}>Cancel</button>
      <button  type="submit" className="add">Add Answer </button>
    </div>

 </Modal>


  <div className="question">
  </div>
  <div className="footer">
 <div className="foot">
<ArrowUpwardOutlined />
<ArrowDownwardOutlined />
 </div>
 <RepeatOneOutlined />
 <ChatBubbleOutlined />
 <div className="Left">
<ShareOutlined />
<MoreHorizOutlined />
 </div>
  </div>

</div>
<div className="answer">
 <p className="number">1 Answer</p>
<div className="answerbox">
  <div className="answered">

 <div className='ans_info'>
 <Avatar/>
<p>Username</p>
<small className="anstime">Timestamp</small>
 </div>
 <div className='ans'>This is the answer</div>
  </div>
</div>
 </div>
 </div>
 </>
  );
};

export default Question;