import React, { useState } from "react";
import {
  ExpandMore,
} from "@mui/icons-material";
import Closeicon from "@mui/icons-material/Close";
import { Avatar, Button, TextField } from "@mui/material";
import { Modal } from "react-responsive-modal";
import "react-responsive-modal/styles.css";
import './css/qnavbar.css'
import 'bootstrap/dist/css/bootstrap.min.css';
function Qnavbar(){
  const [Modalopen,setModalopen] = useState(false)
  const [inputUrl, setinputurl] = useState("")
  const [q, setq] = useState("");
  const Close=<Closeicon/>
  return (
    <nav className="navbar navbar-expand-lg bg-body-tertiary">
    <div className="container-fluid">
  <a className="navbar-brand" href="#">Samvada</a>
  <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
<span className="navbar-toggler-icon"></span>
  </button>
  <div className="collapse navbar-collapse" id="navbarSupportedContent">
<ul className="navbar-nav me-auto mb-2 mb-lg-0">
    <li className="nav-item">
  <a className="nav-link active" aria-current="page" href="#">Home</a>
    </li>
    <li className="nav-item">
  <a className="nav-link" href="#">Following</a>
    </li>
    <li className="nav-item">
  <a className="nav-link" href="#">People</a>
    </li>
    <li className="nav-item">
  <a className="nav-link" href="#">Notification</a>
    </li>
    <li className="nav-item">
  <a className="nav-link disabled" aria-disabled="true">Disabled</a>
    </li>
</ul>
<form className="d-flex search" role="search">
    <input className="form-control me-2 inputbox" type="search" placeholder="Search" aria-label="Search" width="15px"/>
    <button className="btn btn-outline-success" type="submit">Search</button>
</form>
<img className="profile" src="" width={30} height={20} alt="Profile"/>
<button class="btn btn-primary" onClick={() => setModalopen(true)}>Add a question</button>
  </div>
    </div>

    <Modal
  open={Modalopen}
  Closeicon={Close}
  onClose={() => setModalopen(false)}
  CloseonEsc
  center
  closeOnOverlayClick={false}
  styles={{
overlay: {
    height: "auto",
},
  }}
    >
  <div className="title">
<h5>Add question</h5>
<h5>Share Link</h5>
  </div>
  <div className="info">
<Avatar className="avatar" />
<div className="scope">
    <p>Public</p>
    <ExpandMore />
</div>
  </div>
  <div className="Field">
<input value={q} onChange={(e) => setq(e.target.value)} type="text" placeholder="Enter your Question"/>
<div style={{ display: "flex", flexDirection: "column" }}>
    <input type="text" value={inputUrl} onChange={(e) => setinputurl(e.target.value)} placeholder=""
  style={{
margin: "5px 0",
border: "1px solid lightgray",
padding: "10px",
outline: "2px solid #000",
  }} 
    />
    {inputUrl !== "" && (
  <img style={{ height: "40vh", objectFit: "contain" }} src={inputUrl} alt="displayimage"/>
    )}
</div>
  </div>
  <div className="buttons">
<button className="cancel" onClick={() => setModalopen(false)}>
    Cancel
</button>
<button type="submit" className="add">
    Add question
</button>
  </div>    
    </Modal>
</nav>

  
  )
}

export default Qnavbar;
